import React from 'react';
import './About.css';
import Image from './assets/background.jpg';
import { ReactComponent as Html } from './assets/logo-html5.svg';
import { ReactComponent as Css } from './assets/logo-css3.svg';
import { ReactComponent as Js } from './assets/logo-javascript.svg';
import { ReactComponent as Node } from './assets/logo-nodejs.svg';
import { ReactComponent as ReactJs } from './assets/logo-react.svg';
import { ReactComponent as Python } from './assets/logo-python.svg';
import { ReactComponent as Aws } from './assets/logo-amazon.svg';
import Hospo from './assets/hospo.jpg';
import Retail from './assets/retail.jpg';
import Workplace from './assets/workplace.jpg';

const About = () => {
    return (
        <div className="about" id='top'>
            <div className='hero' style={{backgroundImage: `url(${Image})`}}>
                <div className='content'>
                    <h1>Hi, I'm Jacob</h1>
                    <p>I develop data-centered web apps for your business</p>
                    <a href='#bio'>Delve into my work</a>
                </div>
            </div>
            <div className="bio" id='bio'>
                <h2>A little bit about me</h2>
                <p>
                    <s>Officially, I'm a developer based in Australia. I create interactive web applications and systems using data analytics and web technologies such as React, Node.js and Python.</s><br /><br />
                    This is a boring way of saying that I make <b>impact with big data and web tech</b> all from my home in the land down under.
                </p>
                <p>I have been solving complex problems for businesses and clients in <u>three main industries</u> for over half a decade.</p>
            </div>
            <div className='jobs'>
                <div className='tile'>
                    <img src={Hospo} alt='Restaurant'/>
                    <h2>Hospitality</h2>
                </div>
                <div className='tile'>
                    <img src={Retail} alt='Retail Shop'/>
                    <h2>Retail</h2>
                </div>
                <div className='tile'>
                    <img src={Workplace} alt='Professional Workplace'/>
                    <h2>Professional Services</h2>
                </div>
            </div>
            <div className='tech-sec'>
                <h2>What I use to make things work</h2>
                <ul className='tech'>
                    <li><Html className='html-icon' />HTML5</li>
                    <li><Css className='css-icon' />CSS3</li>
                    <li><Js className='js-icon' />JS</li>
                    <li><ReactJs className='react-icon' />React</li>
                    <li><Node className='node-icon' />Node.js</li>
                    <li><Python className='python-icon' />Python</li>
                    <li><Aws className='aws-icon' />AWS</li>
                </ul>
            </div>
        </div>
    );
};

export default About;