import React from 'react';
import './Contact.css';
import { ReactComponent as LinkedIn } from './assets/logo-linkedin.svg';
import { ReactComponent as Github } from './assets/logo-github.svg';
import { ReactComponent as Email } from './assets/email-icon.svg';

const Contact = () => {
    return (
        <div className='contactMe'>
            <h1>Get in contact with me</h1>
            <p>If you are interested in working with me, please reach out:</p>
            <div className='contactIcons'>
                <a href='https://www.linkedin.com/in/jacobh-sejah/' target="_blank" rel="noopener noreferrer"><LinkedIn className='linkedin-icon' /></a>
                <a href='https://github.com/sejah' target="_blank" rel="noopener noreferrer"><Github className='icon' /></a>
                <a href='mailto:jacobhayes@outlook.com.au' target="_blank" rel="noopener noreferrer"><Email className='icon' /></a>
            </div>
            <div className='footer'>
                <a href='#top' className='back-to-top'>Back to top</a>
            </div>
        </div>
    );
};

export default Contact;
